<template>
	<setup title="Select Identity" :error="error" :success="success" :hidePageTitle="hidePageTitle">
		<form class="card-body" method="post" @submit.prevent="handleFormSubmit">
			<div class="default-form w-border" style="text-align: center; margin: auto">
				<div class="form-row label" v-cloak>
					<div class="form-label">Company</div>
					<select
						id="company"
						v-model="company"
						v-bind:disabled="availableCompanies.length === 1"
						v-bind:onchange="setRolePickList"
						name="company"
						style="width: 500px; box-sizing: content-box">
						<option value="null" disabled selected>Please select a company</option>
						<option v-for="c in availableCompanies" v-bind:value="c.CompanyId" :key="c.CompanyId">
							{{ c.Company.Name }} ({{ c.Company.Id }})
						</option>
					</select>
				</div>
				<div class="form-row label" v-if="showCompanyRoles" v-cloak>
					<div class="form-label">Role</div>
					<select
						id="userRole"
						v-model="userRole"
						v-bind:disabled="availableCompanyRoles.length === 1"
						name="userRole"
						style="width: 500px; box-sizing: content-box">
						<option value="null" disabled selected>Please select a role</option>
						<option v-for="role in availableCompanyRoles" v-bind:value="role.Code" :key="role.Code">
							{{ role.Description }} ({{ role.Code }})
						</option>
					</select>
				</div>
				<div class="mt-4" style="text-align: center">
					<div class="spinner small light" v-show="loading"></div>
					<input
						type="submit"
						value="Continue"
						class="button large blue"
						v-show="!loading"
						v-cloak />
				</div>
			</div>
		</form>
	</setup>
</template>

<script>
	import Setup from "./Setup.vue";
	export default {
		name: "Identity",
		data() {
			return {
				error: "",
				success: "",
				loading: false,
				userName: "",
				redirectUrl: "",
				availableCompanies: [],
				availableCompanyRoles: [],
				instructionsMessage: "",
				emailUser: null,
				hidePageTitle: false,
				company: null,
				userRole: null,
			};
		},
		components: {
			Setup,
		},
		computed: {
			showCompanyRoles() {
				return this.company && this.availableCompanyRoles.length >= 1;
			},
			setRolePickList() {
				const roles = this.availableCompanies.find((c) => c.Company.Id === this.company);
				this.availableCompanyRoles = roles ? roles.Roles : [];
			},
		},
		methods: {
			handleFormSubmit() {
				let data = {
					Company: this.company,
					Role: this.userRole,
					RedirectUrl: this.$route.query.redirectUrl,
				};
				this.error = "";
				if (data.Company == "") {
					this.error = "Please select a company.";
				}
				if (data.Role == "") {
					this.error = "Please select a Role.";
				}
				if (this.error != "") return;

				this.loading = true;

				this.$ajax
					.post("/Roles/authenticated-redirect", data)
					.then((resp) => {
						const {RedirectUrl, Message} = resp.data;
						this.success = Message;
						window.setTimeout(() => {
							window.location.href = RedirectUrl;
						}, 1000);
					})
					.catch((err) => {
						if (err.status == 401) {
							this.$router.push({
								name: "Login",
								query: {redirectUrl: this.redirectUrl || undefined},
							});
						}
					})
					.finally(() => {
						this.loading = false;
					});
			},
		},
		async mounted() {
			const {Roles} = await this.$ajax.get("/Roles/details");
			this.availableCompanies = Roles;
			if (Roles.length === 1) {
				this.company = Roles[0].Company.Id;
				this.availableCompanyRoles = Roles[0].Roles;
				if (Roles[0].Roles.length === 1) {
					this.userRole = Roles[0].Roles[0].Code;
				}
			}
		},
	};
</script>

<style scoped>
	.form-label {
		font-size: 12px;
		margin-bottom: 4px;
		line-height: 1.25;
	}
</style>
